    @use "global" as *;

    .main {
    background: $bg;
    }

    .detail-inner {
    @include mq(sp) {
        padding: 0 rem(10);
        margin-bottom: rem(50);
    }
    }

    .portfolio-detail {
    background: $white;
    border: 0.5px solid #333;
    max-width: 1000px;
    margin: rem(43) auto rem(80);
    padding: rem(38) rem(50) rem(80);

    @include mq(sp) {
        padding: rem(10) rem(10);
        margin: rem(56) auto;
    }
    }

    .pf-meta {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: rem(8);
    margin-bottom: rem(26);

    @include mq(sp) {
        position: relative;
        margin-bottom: rem(20);
    }
    }
    .pf-date {
    display: flex;
    align-items: flex-start;
    font-family: $second-font;
    font-size: rem(13);
    color: $black;
    padding-top: rem(40);
    margin-bottom: rem(14);

    @include mq(sp) {
        padding-top: rem(30);
        margin-top: rem(4);
    }
    }

    .label-group {
    display: flex;
    gap: rem(10);
    flex-wrap: wrap;
    }

    .label {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: rem(2) rem(8);
    font-size: rem(12);
    width: 70px;
    text-align: center;

    &.new {
        background: $white;
        color: #d00;
        border: 0.5px solid $main;
    }
    &.wp {
        background: $black;
        color: $white;
        border: 0.5px solid $black;
        font-size: rem(10);
    }
    &.blue {
        background: #1c51ff;
        color: $white;
        border: 0.5px solid #1c51ff;
    }
    &.luxury {
        background: #d9d9d9;
        color: $main;
        border: 0.5px solid #d9d9d9;
    }
    }

    input[type="checkbox"] {
    display: none;
    }
    .favorite {
    position: relative;
    cursor: pointer;
    margin-left: auto;
    margin-right: rem(8);

    svg {
        width: rem(30);
        height: rem(30);
        fill: #1e1e1e;
    }

    label {
        position: relative;
        display: block;
        cursor: pointer;
        width: rem(30);
        height: rem(30);
    }

    @include mq(sp) {
        position: absolute;
        width: rem(30);
        height: rem(30);
        top: rem(-50);
        right: 0;
        margin-left: 0;
        z-index: 2;

        svg {
        width: 100%;
        height: 100%;
        }
    }
    }

    input[type="checkbox"] + label svg {
    fill: #333;
    opacity: 0.3;
    transition: all 0.3s;
    }

    input[type="checkbox"]:checked + label svg {
    fill: #ff6d6d;
    opacity: 1;
    animation: like 0.8s ease-out;
    transition: all 0.3s;
    }

    @keyframes like {
    0% {
        transform: scale(1, 1);
    }
    50% {
        transform: scale(0.5, 0.5);
    }
    100% {
        transform: scale(1, 1);
    }
    }

    input[type="checkbox"]:checked + label::after {
    content: "";
    position: absolute;
    width: rem(50);
    height: rem(50);
    border: 2px solid #fa9797;
    border-radius: 50%;
    animation: circle 0.8s ease-out;
    opacity: 0;
    top: rem(-10);
    left: rem(-10);
    z-index: 1;

    @include mq(sp) {
        width: rem(30);
        height: rem(30);
        border: 1px solid #fa9797;
        top: rem(0);
        left: rem(0);
    }
    }

    @keyframes circle {
    0% {
        transform: scale(0.2, 0.2);
    }
    50% {
        transform: scale(0.5, 0.5);
    }
    80% {
        transform: scale(1, 1);
        opacity: 0.8;
    }
    100% {
        transform: scale(1.3, 1.3);
        opacity: 0;
    }
    }

    .thumbnail {
    border: 1px solid $black;
    height: rem(350);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: rem(6);
    font-size: rem(24);
    color: #333;

    @include mq(tb) {
        height: rem(200);
        font-size: rem(16);
    }

    @include mq(sp) {
        height: rem(200);
        font-size: rem(16);
    }
    }

    .site-url {
    color: $black;
    margin-bottom: rem(36);
    font-size: rem(16);
    }

    .user-row {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: rem(20);

    @include mq(sp) {
        gap: rem(10);
    }
    }

    .pf-icon {
    width: rem(80);
    height: rem(80);
    border-radius: 50%;
    background: #d9d9d9;
    object-fit: cover;
    display: flex;
    align-items: center;
    justify-content: center;

    @include mq(sp) {
        width: rem(50);
        height: rem(50);
    }
    }

    .user-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    }

    .user-name {
    font-family: $base-font;
    font-size: rem(32);
    @include mq(sp) {
        font-size: rem(18);
    }
    }
    .user-role {
    font-family: $base-font;
    font-size: rem(24);
    margin-top: rem(8);

    @include mq(sp) {
        font-size: rem(16);
    }
    }

    .pf-xicon {
    margin-left: auto;
    margin-top: rem(50);

    @include mq(tb) {
        margin-top: 0;
    }
    @include mq(sp) {
        margin-top: 0;
    }

    img {
        width: rem(40);
        height: rem(40);
        max-width: 100%;
        display: block;
    }
    }

    .point-box {
    border: 0.3px solid $main;
    padding: rem(30) rem(20) rem(24);
    margin-top: rem(32);
    margin-bottom: rem(26);

    @include mq(sp) {
        padding: rem(20) rem(10) rem(30);
        margin-top: rem(24);
        margin-bottom: rem(30);
    }
    }

    .point-title {
    margin-bottom: rem(10);
    font-size: rem(24);
    }

    .point-text {
    font-size: rem(16);
    }

    .comment-box {
    padding: rem(30) rem(22);
    background: $main;
    color: $white;

    @include mq(sp) {
        margin-bottom: rem(26);
        padding: rem(30) rem(10) rem(30);
    }
    }

    .comment-title {
    margin-bottom: rem(8);
    font-size: rem(24);
    }

    .comment-text {
    font-size: rem(16);
    }

    .pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: rem(115);
    width: 100%;
    margin: rem(32) auto rem(78);

    @include mq(sp) {
        flex-direction: column;
        gap: rem(30);
        margin: rem(58) auto rem(66);
    }
    }

    .prev,
    .next {
    display: flex;
    align-items: center;
    font-family: $second-font;
    font-size: rem(24);
    cursor: pointer;
    gap: rem(10);

    @include mq(sp) {
        font-size: rem(18);
        gap: rem(20);
    }

    &:hover {
        .prev-border,
        .next-border {
        background: transparent;

        &::before {
            transform: scale(1, 1);
        }
        }
    }
    }

    .prev-border,
    .next-border {
    width: 50px;
    height: 1px;
    background: #888;
    display: inline-block;
    position: relative;
    transition: background 0.3s;

        &::before {
        content: "";
        width: 75px;
        height: 1px;
        position: absolute;
        bottom: 0;
        transform-origin: right top;
        transform: scale(0, 1);
        transition: transform 0.5s;
        background: linear-gradient(90deg, #333, #fff);
        }
    }
    .prev-border {
    &::before {
        right: 0;
    }
    }

    .next-border {
    &::before {
        left: 0;
        transform-origin: left top;
        background: linear-gradient(90deg, #fff, #333);
    }
    }

    .pfsection-line {
    position: relative;
    width: 75%;
    height: 0.5px;
    background: $main;
    margin: 0 auto;
    margin-bottom: rem(1);
    z-index: 1;
    @include mq(sp) {
        margin: 0 auto rem(56);
    }
    }

    .pf-gallery {
    max-width: rem(670);
    padding-top: rem(80);
    padding-bottom: rem(106);
    margin: 0 auto;
    background: $bg;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include mq(sp) {
        padding: 0 rem(10);
    }
    }

    .pf-card-section-title {
    max-width: rem(670);
    display: inline-block;
    font-family: $second-font;
    font-weight: 400;
    font-size: rem(24);
    margin-bottom: rem(16);
    width: 100%;
    align-self: flex-start;

    @include mq(sp) {
        max-width: rem(335);
        width: 100%;
        margin-left: auto;
        margin-right: auto;
    }
    }

    .pf-card-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: rem(20) rem(20);
    margin-bottom: rem(54);

    @include mq(tb) {
        grid-template-columns: repeat(3, 1fr);
    }

    @include mq(sp) {
        grid-template-columns: 1fr;
        margin-bottom: rem(68);
        gap: rem(30);
        width: 100%;
        margin-left: auto;
        margin-right: auto;
    }
    }

    .pf-card {
    background: white;
    border: 1px solid #333;
    box-shadow: 2px 2px 4px 0px rgba(51, 51, 51, 0.5);
    width: rem(220);
    height: rem(230);
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: rem(10);
    margin-left: auto;
    margin-right: auto;
    opacity: 0;
    transform: translateY(rem(40));
    transition: opacity 1s ease-out, transform 1s ease-out;
    cursor: pointer;

    &.is-inview {
        opacity: 1;
        transform: translateY(0);
    }

    @include mq(sp) {
        width: rem(335);
        height: rem(350);
        padding: rem(20) rem(20);
        margin: 0 auto;
    }
    }

    .pf-card__image {
    width: rem(200);
    height: rem(180);
    background: #d9d9d9;
    margin-bottom: rem(20);
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    @include mq(sp) {
        width: rem(295);
        height: rem(270);
    }
    }

    .pf-card__title {
    font-size: rem(16);
    margin-top: rem(-7);
    color: #333;
    letter-spacing: 0.05em;
    text-align: left;
    }
