@use "global" as *;

.concept-wrapper {
  overflow: hidden;
}

.about {
  background-color: $bg;
}

.concept-wrapper {
  padding-block: rem(90) rem(80);
  margin-bottom: rem(160);
}

.concept__contents {
  position: relative;
  margin-top: rem(60);
  z-index: 0;

  @include mq(sp) {
    margin-top: rem(175);
  }

  &::after {
    content: "";
    background: #d9d9d9;
    position: absolute;
    top: 83%;
    right: calc(0% + rem(50));
    width: calc(100vw - rem(50) - rem(220)); // 画面幅から幅調整（50px）とinner(220px)を引いた幅
    height: rem(180);
    z-index: -1;

    @include mq(sp) {
      left: calc(0% - rem(20));
      width: 90%;
    }
  }
}

.concept__img--inner {
  clip-path: inset(0);
  width: rem(800);
  height: rem(664);

  @media screen and (min-width: 550px) and (max-width: 750px) {
    height: rem(500);
  }
}

.concept__img {
  background: url("../img/about_concept.webp") no-repeat -150px center / cover;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1;
  margin-inline: auto;
  box-shadow: 8px 8px 4px 0px #00000080;

  @include mq(sp) {
    background: url("../img/about_concept.webp") no-repeat 60% center / cover;
    margin-left: calc(50% - 50vw);
  }

  @media screen and (max-width: 1000px) {
    background: url("../img/about_concept.webp") no-repeat 90% center / cover;
  }
}

.concept__main {
  background: rgba(51, 51, 51, 0.6);
  position: absolute;
  top: 28%;
  left: 15%;
  width: rem(850);
  height: fit-content;
  color: $white;
  padding: rem(45) rem(35);

  @include mq(sp) {
    top: rem(-115);
    left: auto;
    right: rem(-15);
    min-width: rem(350);
    width: 100%;
  }

  &::before {
    content: "";
    background: url("../img/about_concept_person.webp") no-repeat center center/cover;
    position: absolute;
    // top: -35%;
    top: rem(-120);
    left: 5%;
    width: rem(150);
    height: rem(150);

    @include mq(sp) {
      top: rem(-105);
      left: auto;
      right: 0;
      width: rem(130);
      height: rem(130);
    }
  }
}

.concept__main--title {
  height: rem(42);
  font-size: rem(32);
  font-weight: $bold;

  @include mq(sp) {
    font-size: rem(20);
  }
}

.concept__main--subs {
  margin-top: rem(30);
  line-height: rem(32);
  letter-spacing: rem(0.48);
}

.concept__main--sub span {
  @include mq(sp) {
    display: inline;
  }
}

.concept__main--sub:nth-child(n + 2) {
  margin-top: rem(40);
}

.member-wrapper {
  margin-bottom: rem(160);
}

.member__cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: rem(100) rem(105);
  padding-bottom: rem(300);
  position: relative;
  margin-top: rem(60);

  @include mq(sp) {
    grid-template-columns: repeat(1, 1fr);
    gap: rem(40);
    padding-bottom: 0;
    max-width: rem(350);
    margin-inline: auto;
  }

  &::after {
    content: "Folio Spot";
    position: absolute;
    top: rem(250);
    left: -26%;
    writing-mode: vertical-rl;
    font-size: rem(200);
    color: #d9d9d9;
    font-family: $second-font;
    z-index: -1;

    @include mq(sp) {
      font-size: rem(150);
      left: -50%;
    }

    @media screen and (max-width: 500px) {
      display: none;
    }
  }
}

.member__card:nth-child(even) {
  top: rem(300);

  @include mq(sp) {
    top: rem(0);
  }
}

.member__card {
  position: relative;
  display: grid;
  grid-template-columns: rem(150) 1fr;
  gap: rem(30) rem(20);
  grid-template-areas:
    "icon profile"
    "text text";
  border: 0.5px solid #333;
  box-shadow: 4px 4px 4px 0px rgba(0, 0, 0, 0.25);
  padding: rem(30);
  background: $white;

  @include mq(sp) {
    grid-template-columns: rem(120) 1fr;
    gap: rem(20) rem(18);
    padding: rem(20) rem(10);
  }
}

.member__card-icon {
  width: 100%;
  height: auto;
  grid-area: icon;
}

.member__card-profile {
  grid-area: profile;
}

.member__card-profile--name {
  font-size: rem(32);
  color: $black;

  @include mq(sp) {
    font-size: rem(24);
  }
}

.member__card-profile--role {
  font-size: rem(22);
  margin-top: rem(6);
  color: $black;

  @include mq(sp) {
    font-size: rem(14);
  }
}

.member__card-profile--link {
  display: flex;
  gap: rem(16);
  margin-top: rem(22);

  a,
  a img,
  a svg {
    width: rem(40);
    height: rem(40);
    transition: all 0.3s ease;

    &:hover {
      opacity: 0.8;
    }

    @include mq(sp) {
      width: rem(36);
      height: rem(36);
    }
  }
}

a img,
a svg {
  transition: all 0.3s ease;

  &:hover {
    opacity: 0.7;
  }
}

.member__card-text {
  grid-area: text;
  font-size: rem(20);
  color: $black;
  line-height: 2;
  padding: rem(20);
  border: 0.5px solid #333;

  @include mq(sp) {
    padding: rem(16);
    font-size: rem(16);
  }
}
