@use "global" as *;

.nav-menu-wrapper {
  position: sticky; //fv固定
  height: 0;
  width: rem(80);
  margin-left: auto;
  top: 0;
  z-index: 999;

  @include mq(sp) {
    width: rem(50);
  }

  &.top-fv__nav {
    @include mq(pc) {
      position: absolute; //fv固定
      top: 0;
      right: 0;
      height: rem(600);
    }
  }
}

@include mq(sp) {
  .nav-menu-wrapper.nav-menu-active {
    position: fixed;
    right: 0;
    height: rem(60);
    display: grid;
    place-content: center;
    // width: rem(70);

    .nav-menu__position {
      height: 100%;
      display: flex;
      align-items: center;
    }

    .nav-menu__position > .nav-menu__icon {
      height: rem(50);
      width: rem(50);
      background: transparent;
      transition: none;
      svg {
        transform: translate(0%, 0%);
      }
    }

    .nav-menu__icon--title {
      display: none;
    }
  }
}
@include mq(pc) {
  .nav-menu-wrapper.nav-menu-active > .nav-menu__position > .nav-menu__icon {
    height: rem(400);
  }
  .nav-menu-wrapper.nav-menu-active {
    .nav-menu__icon--title {
      display: none;
    }
  }
}

.nav-menu__position {
  position: relative;
  height: rem(80);
  margin-left: auto;

  @include mq(sp) {
    height: rem(50);
  }

  &.top-fv__nav--position {
    @include mq(pc) {
      height: 100%;
      transition: all 0.3s ease 0s;
    }
  }
}

.nav-menu__icon {
  background: $main;
  align-items: center;
  justify-content: center;
  display: flex;
  transition: all 0.3s ease 0s;
  height: 100%;

  @include mq(pc) {
    &:hover {
      background-color: $main;
      height: rem(400);
    }
  }

  @include mq(sp) {
    width: 100%;
    padding: rem(11);
    z-index: 999;
    position: relative;
  }

  svg {
    position: absolute;
    transition: opacity 0.3s ease;
    transform: translate(0%, -20%);
  }

  &.top-fv__nav--icon {
    @include mq(pc) {
      height: rem(600) !important;

      &:hover {
        opacity: 0.9;
      }
    }
  }
}

#js_nav-icon__logo {
  width: rem(40);
  height: rem(40);

  @include mq(sp) {
    width: rem(25);
    height: rem(25);
  }
}

#js_nav-icon__cross {
  width: rem(40);
  height: rem(40);
  padding: rem(4);

  @include mq(sp) {
    width: rem(25);
    height: rem(25);
    padding: rem(4);
  }
}

.nav-menu__icon--title {
  color: $white;
  font-size: rem(20);
  font-family: $second-font;
  margin-top: rem(40);

  @include mq(sp) {
    // display: none;
    font-size: rem(16);
    margin-top: rem(25);
  }
}

.nav-menu__list {
  font-size: rem(16);
  position: absolute;
  top: 0;
  height: rem(400);
  right: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  z-index: 10;
  overflow: hidden;
  display: none;

  @include mq(sp) {
    top: rem(60);
    right: 0;
    width: 100vw;
    height: calc((100dvh - rem(60)));
  }

  &.top-fv__nav--list {
    height: rem(600);
    @include mq(sp) {
      height: calc(100dvh - rem(60));
    }
  }
}

.nav-menu__list--item {
  flex-grow: 1;
  z-index: 1;
  display: flex;
  background: $white;
  font-family: $third-font;
  background: #888888;

  @include mq(pc) {
    &:hover {
      background: $main;
    }
  }

  a {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 100%;
    width: rem(600);
    height: 100%;
    text-align: center;
    color: $white;
    font-size: rem(48);
    padding-inline: rem(20);

    @include mq(sp) {
      width: 100%;
    }
  }
}

.nav-menu__list--about {
  a {
    background: url("../img/nav_about-pc.webp") no-repeat bottom center;
    background-size: cover;
  }
}

.nav-menu__list--portfolio {
  a {
    background: url("../img/nav_portfolio-pc.webp") no-repeat bottom center;
    background-size: cover;
  }
}
.nav-menu__list--search {
  a {
    background: url("../img/nav_search-pc.webp") no-repeat bottom center;
    background-size: cover;
  }
}
.nav-menu__list--contact {
  a {
    background: url("../img/nav_contact-pc.webp") no-repeat bottom center;
    background-size: cover;
  }
}

.nav-menu__mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #333;
  opacity: 0;
  display: none;
  z-index: 900;
}

.js_nav-menu_active {
  opacity: 1;
}

.js_nav-menu_inactive {
  opacity: 0;
  visibility: hidden;
}

.nav-palette {
  display: none;
  position: fixed;
  width: rem(1000);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1100;

  @include mq(sp) {
    width: 95%;
  }
}

.nav-palette.is-active {
  display: block;
}

.nav-palette {
  .palette__inner {
    padding: 0;
  }
  .palette__select-area {
    margin: 0;
  }

  @include mq(sp) {
    .palette__select-area {
      padding: rem(15);
    }

    .nav-palette.is-active {
      display: block;
    }

    .palette__style--title,
    .palette__color--title,
    .palette__search-btn {
      margin-top: rem(20);
    }

    .palette__color--lists {
      margin-top: rem(10);
    }
  }
}

.nav-palette__mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #333;
  opacity: 0;
  display: none;
  z-index: 1000;
}

.nav-palette__close-btn {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(50%, -50%);
  width: rem(40);
  height: rem(40);
  display: grid;
  place-items: center;
  background: $main;
  border-radius: 50%;

  @include mq(sp) {
    top: rem(7.5);
    right: rem(15);
  }
}

.nav-close-line {
  width: rem(20);
  height: rem(2);
  background: $white;
  position: absolute;
  top: 50%;
  left: 25%;
}

.nav-palette__close-line1 {
  rotate: 45deg;
}

.nav-palette__close-line2 {
  rotate: -45deg;
}
