@use "global" as *;

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;

  //~375px
  @media screen and (max-width: $sp) {
    font-size: vw(strip-unit($sp), 16);
  }

  //376px~767px
  @media screen and (min-width: ($sp + 1)) and (max-width: ($tb - 1)) {
    font-size: 16px;
  }

  //768px~1439px
  @media screen and (min-width: $tb) and (max-width: ($pc - 1)) {
    font-size: vw(strip-unit($pc), 16);
  }

  //1440px~
  @media screen and (min-width: $pc) {
    font-size: 16px;
  }
}

body {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  color: $text;
  background-color: $white;
  font-family: $base-font;
}

main {
  flex-grow: 1;
}

.is-pc {
  display: block;
  @include mq(sp) {
    display: none;
  }
}

.is-sp {
  display: none;
  @include mq(sp) {
    display: block;
  }
}
