.no-scroll {
  overflow: hidden;
}

.section__title--main {
  font-size: rem(64);
  font-family: $second-font;
  color: $black;
}
.section__title--sub {
  font-size: 24px;
  color: $black;
}

.pagination__index {
  display: flex;
  gap: rem(20);
}

.pagination__index--item {
  width: rem(40);
  height: rem(40);
  display: grid;
  place-items: center;

  a {
    font-family: $second-font;
    color: $main;
  }
}

.pagination__index--item.is-active {
  background: $white;
  box-shadow: 4px 4px 4px 0px rgba(0, 0, 0, 0.25);
}
