@use "global" as *;

.header {
  width: 100%;
  height: rem(300);
  position: relative;
  color: $white;
  font-family: $second-font;

  @include mq(sp) {
    height: 150px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -2;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #333;
    opacity: 0.7;
    z-index: -1;
  }

  // 各ページの背景画像
  &.header--portfolio::before {
    background-image: url(../img/portfolio-top.png);
  }
  &.header--about::before {
    background-image: url(../img/about-top.png);
  }
  &.header--detail::before {
    background-image: url(../img/detail-top.png);
  }
  &.header--category::before {
    background-image: url(../img/category-top.png);
  }
  &.header--contact::before {
    background-image: url(../img/contact-top.png);
  }
  &.header--tag::before {
    background-image: url(../img/tag-top.png);
  }
  &.header--search::before {
    background-image: url(../img/search-top.png);
  }

  &.header--404 {
    height: rem(110);
    @include mq(sp) {
      height: rem(80);
    }

    &::after {
      background-color: #222;
      opacity: 0.9;
    }
  }

  // 各ページの背景画像
  &.header--portfolio::before {
    background-image: url(../img/portfolio-top.png);
  }
  &.header--about::before {
    background-image: url(../img/about-top.png);
  }
  &.header--detail::before {
    background-image: url(../img/detail-top.png);
  }
  &.header--contact::before {
    background-image: url(../img/contact-top.png);
  }
}

.header__title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: rem(64);
  font-weight: 400;
  text-align: center;
  z-index: 1;

  @include mq(sp) {
    font-size: rem(32);
  }
}
