@use "global" as *;

.thx__contents {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: rem(80);
  padding-bottom: rem(160);

  @include mq(sp) {
    flex-direction: column;
    padding-block: rem(70) rem(120);
    gap: rem(20);
  }

  img {
    width: rem(300);
    height: auto;
  }
}

.thx__title {
  font-size: rem(72);
  font-family: $second-font;
  font-weight: $normal;
  color: $black;

  @include mq(sp) {
    font-size: rem(64);
  }
}
