@use "global" as *;

.category-gallery {
    padding-top: rem(44);
    padding-bottom: rem(160);
    margin: 0 auto;
    background: $bg;
    display: flex;
    flex-direction: column;
    align-items: center;

@include mq(sp) {
    padding-top: rem(56);
    padding-bottom: rem(40);
}
}

.category-gallery__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: rem(30) rem(30);
    margin-bottom: rem(80);

    @include mq(tb) {
        grid-template-columns: repeat(2, 1fr);
    }

    @include mq(sp) {
        grid-template-columns: 1fr;
        margin-bottom: rem(20);
        gap: rem(10);
        width: 100%;
        margin-left: auto;
        margin-right: auto;
    }
}

.category-gallery__card {
    background: white;
    border: 1px solid #333;
    box-shadow: 2px 2px 4px 0px rgba(51, 51, 51, 0.5);
    width: rem(335);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: rem(20);
    opacity: 0;
    transform: translateY(rem(40));
    transition: opacity 1s ease-out, transform 1s ease-out;
    cursor: pointer;

    &.is-inview {
        opacity: 1;
        transform: translateY(0);
    }

    @include mq(sp) {
        width: 100%;
        max-width: rem(335);
        margin: 0 auto rem(24);
        padding: rem(20);
    }
}

.category-gallery__card-image {
    width: rem(295);
    height: rem(270);
    background: #d9d9d9;
    margin-bottom: rem(20);
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    @include mq(sp) {
        width: 100%;
        height: auto;
        aspect-ratio: 295 / 270;
        img {
        width: 100%;
        height: auto;
        object-fit: cover;
        }
    }
}

.category-gallery__card-title {
    font-size: rem(16);
    margin-top: rem(-7);
    color: #333;
    letter-spacing: 0.05em;
    text-align: left;
}

.alllist-button--category {
    margin-top: rem(80);

    @include mq(sp) {
        margin-top: rem(40);
        margin-bottom: rem(80);
    }
}
