@use "global" as *;

.top-hero {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: $main;
  z-index: 9999;
  display: none; //アニメーション動かすときにはコメントアウトしてください。
}

.top-hero__container {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  transform: translate(-50%, -50%);
  font-size: rem(40);
  color: white;
}

.top-hero__title {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: $bold;

  @include mq(sp) {
    display: block;
    flex-direction: column;
    font-size: rem(32);
    text-align: center;
    line-height: 1.5;
  }
}

.top-hero__title1 {
  width: rem(280);
  height: rem(60);
  text-align: left;
  @include mq(sp) {
    width: rem(230);
    height: auto;
    white-space: nowrap;
  }
}
.top-hero__title2 {
  width: rem(320);
  height: rem(60);
  text-align: left;
  @include mq(sp) {
    width: rem(255);
    height: auto;
    white-space: nowrap;
  }
}

.top-hero__title3 {
  width: rem(270);
  height: rem(60);
  text-align: left;
  @include mq(sp) {
    width: rem(195);
    height: auto;
    white-space: nowrap;
  }
}
