@use "global" as *;

.breadcrumb {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: rem(20);
  padding: rem(40) rem(80);
  font-family: $second-font;
  font-size: rem(24);
  color: $text;

  @include mq(sp) {
    gap: rem(20);
    padding: rem(20) rem(30);
    font-size: rem(16);
  }

  a {
    color: inherit;
    transition: all 0.3s ease 0s;

    &:hover {
      opacity: 0.7;
    }
  }
}
