@use "global" as *;

.top-news__container {
  border-top: 1px solid $accent;
  border-bottom: 1px solid $accent;
  background: $white;
  overflow: hidden;
}

.loop-area {
  display: flex;
  align-items: center;
  width: auto;
}

.top-news__list {
  height: rem(48);
  align-items: center;
  display: flex;
  // margin-left: rem(20);
  padding-left: rem(188);
  padding-right: rem(188);
  width: calc(4 / 2) * 100%; // ４回同じ要素を繰り返し、2回目の表示で初期化するため4/2 * 100%;のwidthを設定
}

.top-news__title {
  font-size: rem(16);
  font-weight: $normal;
}

.top-news__list-item {
  white-space: nowrap;
  padding-left: rem(20);

  a {
    color: inherit;
  }
}
