@use "global" as *;

.footer {
  position: relative;
  color: $white;
  padding-top: rem(70);

  @include mq(sp) {
    padding-top: rem(40);
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url(../img/footer-bg.png) center/cover no-repeat;
    z-index: -2;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #222;
    opacity: 0.9;
    z-index: -1;
  }

  &--404,
  &--thx {
    padding-top: 0;
  }
}

.footer__inner {
  max-width: rem(1200);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__title {
  font-family: $second-font;
  font-size: rem(64);
  font-weight: 400;
  color: $white;

  display: flex;
  align-items: center;
  gap: rem(10);

  @include mq(tb) {
    font-size: rem(56);
  }

  @include mq(sp) {
    font-size: rem(48);
    gap: rem(9);
  }

  img {
    width: rem(50);

    @include mq(sp) {
      width: rem(40);
      height: rem(40);
    }
  }
}

.footer__nav-list {
  display: flex;
  margin-top: rem(25);
  margin-bottom: rem(35);
  gap: rem(51);

  @include mq(sp) {
    margin-top: rem(13);
    margin-bottom: rem(16);
    gap: rem(36);
  }
}

.footer__nav-item {
  a {
    color: $white;
    font-size: rem(24);
    font-weight: 400;
    font-family: $second-font;
    transition: opacity 0.3s;

    &:hover {
      opacity: 0.7;
    }

    @include mq(tb) {
      font-size: rem(21);
    }

    @include mq(sp) {
      font-size: rem(18);
    }
  }
}

.footer-line {
  width: rem(321);
  height: rem(0.5);
  background-color: #888888;

  @include mq(sp) {
    width: rem(247);
  }
}

.footer__sns-list {
  display: flex;
  gap: rem(15);
  margin-top: rem(40);
  margin-bottom: rem(86);

  @include mq(sp) {
    margin-top: rem(18);
    margin-bottom: rem(40);
  }
}

.footer__sns-item {
  text-align: center;

  &-wrap {
    display: flex;
    flex-direction: column;
    gap: rem(7);
    align-items: center;
  }

  a {
    display: block;
    transition: opacity 0.3s;

    &:hover {
      opacity: 0.7;
    }
  }

  img {
    width: rem(40);
    height: auto;
  }
}

.footer__logo-img {
  margin-right: rem(10);
}

.footer__copyright {
  font-family: $second-font;
  font-size: rem(20);
  opacity: 0.7;

  @include mq(sp) {
    font-size: rem(16);
  }
}

.footer__copyright-wrap {
  width: 100vw;
  height: rem(66);
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #333;
  text-align: center;
  margin-left: calc(-1 * #{rem(20)});
  margin-right: calc(-1 * #{rem(20)});

  @include mq(sp) {
    height: rem(40);
  }
}
